.inner {
  transform: translateX(1500px);
  position: absolute;
  background-color: #242426;
  transition: all 0.5s;
  z-index: 99999;
  border-radius: 20px;
  width: 518px;
  height: 100vh;
  padding: 48px 40px;
  right: 0;
  top: 0;
  display: flex;
  flex-direction: column;
  gap: 40px;
}

.themeFilters {
  background-color: #fff;
  border: 1px solid #afb2b6;
}

.activeInner {
  transform: translateX(0);
  position: fixed;
  transition: all 0.5s;
}

.close {
  position: absolute;
  right: 47px;
  top: 41px;
  cursor: pointer;
  width: 16px;
  height: 16px;
  transition: all 0.5s;

  &:hover {
    color: #7b61ff;
    transition: all 0.5s;
    transform: scale(1.5);
  }
}

.sort {
  border-bottom: 1px solid #323537;
  padding-bottom: 32px;
}

.sortTheme {
  border-bottom: 1px solid #afb2b6;
}

.innerSort {
  display: flex;
  justify-content: center;
  margin-top: 10px;
}

.buttonLeft {
  text-align: center;
  background-color: #242426;
  padding: 14px 10px;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  border: 2px solid #323537;
  color: #80858b;
  width: 100%;
  cursor: pointer;
}

.buttonRigth {
  text-align: center;
  background-color: #242426;
  padding: 14px 10px;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  border: 2px solid #323537;
  color: #80858b;
  border-left: none;
  width: 100%;
  cursor: pointer;
}

.buttonTheme {
  background-color: #fff;
  border: 2px solid #afb2b6;
}

.buttonActive {
  background-color: #323537;
  color: white;
  cursor: default;
}

.input {
  width: 100%;
  height: 56px;
  border-radius: 10px;
  padding: 16px 50px 16px 20px;
  border: 2px solid transparent;
  outline: none;
  font-weight: 500;
  color: #fff;
  background-color: #323537;
  margin-top: 8px;
  &:focus {
    border: 2px solid #7b61ff;
  }
  &::placeholder {
    color: #80858b;
  }
}

.genre {
  position: relative;
}

.choose {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #323537;
  width: 100%;
  height: 56px;
  border-radius: 10px;
  padding: 16px 50px 16px 20px;
  border: 1px solid transparent;
  margin-top: 8px;
  cursor: pointer;
  transition: all 0.5s;
  &:hover {
    border: 1px solid #7b61ff;
    transition: all 0.5s;
  }
}

.list {
  width: 438px;
  height: 250px;
  background: #323537;
  border-radius: 10px;
  overflow: auto;
  -ms-overflow-style: none;
  scrollbar-width: none;
  li {
    transition: all 0.5s;

    &:hover {
      color: #917aff;
      transition: all 0.5s;
    }
  }
}

.list::-webkit-scrollbar {
  width: 0;
  height: 0;
}

.listActive {
  display: none;
}

.userActiveInner {
  opacity: 0;
  transform: translateY(0px);
  transition: all 0.9s;
  position: absolute;
  top: 75px;
  width: 267px;
  background-color: #242426;
  font-weight: 500;
  border-radius: 10px;
}

.item {
  padding: 17px 20px 15px 20px;
  border-bottom: 1px solid #323537;
  cursor: pointer;
  &:last-child {
    border-bottom: none;
  }
}

.active {
  color: #7b61ff;
}

.blockModal {
  z-index: 1;
  opacity: 100%;
  transform: translateY(20px);
  transition: all 0.9s;
}

.selectGenre {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 12px;
  background-color: #323537;
  width: 100%;
  min-height: 100px;
  border-radius: 10px;
  padding: 12px;
}

.genreSel {
  background-color: #242426;
  border-radius: 6px;
  padding: 4px 10px 4px 8px;
  height: 32px;
}

.btn {
  display: flex;
  justify-content: space-between;
  gap: 40px;
}

.btnFilter {
  padding: 17px 50px 15px 51px;
}

.closeSvg {
  cursor: pointer;
  &:hover {
    color: #7b61ff;
  }
}

.innerCheck {
  height: 50px;
  display: flex;
  gap: 20px;
}

.btnCheck {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 25px;
  height: 25px;
  background-color: #323537;
  border: 1px solid #7b61ff;
  border-radius: 6px;
}

.InputTheme {
  background-color: #fff;
  border: 2px solid #afb2b6;
  color: #323537;
  li {
    border-bottom: 1px solid #afb2b6;
  }
}

.buttonActivetTheme {
  opacity: 60%;
}

@media (max-height: 935px) {
  .inner {
    padding: 30px 30px;
    gap: 20px;
  }
}

@media (max-height: 935px) {
  .inner {
    padding: 17px 42px;
    gap: 20px;
  }
  .buttonLeft,
  .buttonRigth {
    padding: 10px 5px;
  }
  .sort {
    padding-bottom: 15px;
  }
  .input {
    padding: 10px 50px 10px 20px;
  }
  .innerCheck {
    height: 25px;
  }
  .btnFilter {
    padding: 10px 50px 10px 51px;
  }
}
@media (max-height: 680px) {
}

@media (max-width: 500px) {
  .inner {
    padding: 25px 35px;
  }
  .btnFilter {
    padding: 25px 50px;
  }
}

@media (max-width: 496px) {
  .btnFilter {
    padding: 20px 30px;
  }
  .inner {
    width: 100vw;
    padding: 35px 25px;
    gap: 30px;
  }
  .list {
    width: 350px;
    height: 250px;
  }

  .sort {
    padding-bottom: 25px;
  }
  .selectGenre {
    gap: 0;
    margin-bottom: 0;
    min-height: 50px;
    padding: 10px;
  }
  .innerCheck {
    height: 20px;
    margin-bottom: 20px;
    font-size: 12px;
  }

  .buttonLeft,
  .buttonRigth {
    padding: 10px 5px;
  }
}

@media (max-width: 390px) {
  .list {
    width: 270px;
    height: 200px;
  }
  .btn {
    gap: 20px;
  }
  .btnFilter {
    font-size: 14px;
    padding: 15px 29px;
  }
 
}

@media (max-width: 380px) {
  .inner {
    gap: 16px;
  }

  .btnFilter {
    font-size: 12px;
  }
}
